.carhartt-modal-content-container {
    padding: 25px 200px 50px 50px;
  background-color: rgb(251, 249, 246);
  border-radius: 20px; /* Make the background color have rounded edges */
  margin: 0px;
  justify-content: left;

    font-size: 26px;
    line-height: 40px; /* Adjust this value based on your design preferences */
  
}

.carhartt-section {
    margin-bottom: 1em;
}

/* Additional styles for headers and links */
.carhartt-modal-content-container h2,
.carhartt-modal-content-container h3,
.carhartt-modal-content-container h4 {
    margin-top: 1em;
}

.carhartt-modal-content-container p {
    margin: 1em 0;
}

.carhartt-modal-content-container a {
    display: block;
    margin-top: 1em;
    color: #1a0dab; /* Typical link color */
    text-decoration: none;
}

.carhartt-modal-content-container a:hover {
    text-decoration: underline;
}